import React, {Component} from 'react';
import {Layout, Menu, Row, Col} from 'antd';
import {
    MenuUnfoldOutlined, MenuFoldOutlined, DotChartOutlined,
    RadiusSettingOutlined, RiseOutlined, BarChartOutlined,
    AppstoreOutlined, BuildOutlined, ApartmentOutlined
} from '@ant-design/icons';

import Weather from "./components/Weather"
import AreaChart from "./components/AreaChart"
import BarChart from "./components/BarChart"
import LineChart from "./components/LineChart"
import RadarChart from "./components/RadarChart"
import ShowModels from "./components/ShowModels"

import 'antd/dist/antd.css'
import './App.css';

const { Header, Sider, Content, Footer } = Layout;
const { SubMenu } = Menu;

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            collapsed: false,
            leftSlideWidth: 200,
            colBgHeight: 100,
            showCols: false,
            currentMenu: 1
        }
    }

    toggle = () => {
        this.setState({
            collapsed: !this.state.collapsed,
        });
    };
    handleClick = e => {
        console.log('click ', e);
        this.setState({ currentMenu: e.key }, ()=>{
            if (this.state.currentMenu === "0"){
                var gap = 80;
                if(this.state.collapsed){
                    gap = 200;
                }
                this.setState({
                    collapsed: !this.state.collapsed,
                    leftSlideWidth: gap
                });
            }else{

            }
        });
    };


    /**
     * 开始Three
     *
     * @memberof ThreeBim
     */
    componentDidMount(){
        let colHeight =  Math.floor((document.body.clientHeight - 64) / 3);
        // console.log('colHeight =   ', colHeight);
        this.setState({
            colBgHeight: colHeight,
            showCols: true
        })

    }

    render() {
        return (
            <Layout className={"container"}>
                <Sider trigger={null} collapsible collapsed={this.state.collapsed}
                       width={200}  collapsedWidth={80}
                       style={{
                           overflow: 'auto',
                           height: '100vh',
                           position: 'fixed',
                           left: 0,
                       }}
                       >
                    <div className="logo" >
                        <img src={"logo192.png"}/>
                        {!this.state.collapsed ? (
                            <b>斜拉桥数字化平台</b>
                        ) : null}
                    </div>
                    <Menu theme="dark" mode="inline" defaultSelectedKeys={['1']}>
                        <Menu.Item key="1" icon={<DotChartOutlined />}>
                            几何模型
                        </Menu.Item>
                        <Menu.Item key="2" icon={<RadiusSettingOutlined />}>
                            施工仿真
                        </Menu.Item>
                        <Menu.Item key="3" icon={<RiseOutlined />}>
                            超前预测
                        </Menu.Item>
                        <Menu.Item key="4" icon={<BarChartOutlined />}>
                            当前状态
                        </Menu.Item>
                        <Menu.Item key="7" icon={<ApartmentOutlined />}>
                            工作团队
                        </Menu.Item>

                        <SubMenu key="sub1" icon={<AppstoreOutlined />} title="项目信息">
                            <Menu.Item key="5" icon={<AppstoreOutlined />}>
                                项目信息
                            </Menu.Item>
                            <Menu.Item key="6" icon={<BuildOutlined />}>
                                结构信息
                            </Menu.Item>
                        </SubMenu>

                        <Menu.Item  icon={<BuildOutlined />}>
                            多余的按钮
                        </Menu.Item>
                        <Menu.Item  icon={<BuildOutlined />}>
                            多余的按钮
                        </Menu.Item>
                        <Menu.Item  icon={<BuildOutlined />}>
                            多余的按钮
                        </Menu.Item>
                        <Menu.Item  icon={<BuildOutlined />}>
                            多余的按钮
                        </Menu.Item>
                        <Menu.Item  icon={<BuildOutlined />}>
                            多余的按钮
                        </Menu.Item>
                        <Menu.Item  icon={<BuildOutlined />}>
                            多余的按钮
                        </Menu.Item>
                        <Menu.Item  icon={<BuildOutlined />}>
                            多余的按钮
                        </Menu.Item>
                        <Menu.Item  icon={<BuildOutlined />}>
                            多余的按钮
                        </Menu.Item>
                        <Menu.Item  icon={<BuildOutlined />}>
                            多余的按钮
                        </Menu.Item>

                    </Menu>
                </Sider>
                <Layout className="site-layout"  style={{ marginLeft: this.state.leftSlideWidth}}>
                    <Header className="site-layout-background-header" style={{ padding: 0 }}>
                        <Menu onClick={this.handleClick} selectedKeys={[this.state.currentMenu]} mode="horizontal">
                            <Menu.Item key="0" className={'trigger'} icon={this.state.collapsed ? <MenuUnfoldOutlined/> : <MenuFoldOutlined/>}>
                            </Menu.Item>
                            <Menu.Item key="1" icon={<DotChartOutlined />}>
                                数据页面(1)
                            </Menu.Item>
                            <Menu.Item key="2" icon={<RadiusSettingOutlined />}>
                                数据页面(2)
                            </Menu.Item>
                            <Menu.Item key="3" icon={<RiseOutlined />}>
                                数据页面(3)
                            </Menu.Item>
                        </Menu>
                    </Header>
                    {this.state.showCols ? (
                        <>
                            <Content  className={"water-fall"}>
                                <div className={"water-fall-row"}>
                                    <div className={"coll-small"}  style={{height: this.state.colBgHeight}}>
                                        <Weather />
                                    </div>
                                    <div className={"coll-small"} style={{height: this.state.colBgHeight}}>
                                        <BarChart  containerId={'bar-container'} containerHeight = {this.state.colBgHeight}  />
                                    </div>
                                    <div className={"coll-small"} style={{height: this.state.colBgHeight}}>
                                        <AreaChart containerId={'area-container-1'} containerHeight = {this.state.colBgHeight}/>
                                    </div>
                                </div>
                                <div className={"water-fall-row2"}>
                                    <div className={"coll-small-left"}>
                                        <div className={"coll-small-left-item"} style={{height: this.state.colBgHeight}}>
                                            <LineChart containerId={'dou-area-container'} containerHeight = {this.state.colBgHeight}/>
                                        </div>
                                        <div className={"coll-small-left-item"} style={{height: this.state.colBgHeight}}>
                                            <RadarChart containerId={'radar-container'} containerHeight = {this.state.colBgHeight}/>
                                        </div>
                                    </div>
                                    <div className={"coll-small-right"} style={{height: this.state.colBgHeight * 2}}>
                                        <ShowModels leftSlideWidth={this.state.leftSlideWidth} containerHeight = {this.state.colBgHeight * 2} />
                                    </div>
                                </div>

                            </Content>
                            <Footer className={"footer"} ><b>Design ©2018 Created by SWJTU</b></Footer>
                        </>
                        ) : null}
                </Layout>
            </Layout>
        );
    }
}

export default App;

